.dices {
  margin-bottom: 16px;
}

.dices .dice {
  padding: 4px !important;
  margin-right: 2px;
  margin-bottom: 0 !important;
}

.selectable {
  cursor: pointer;
}

.selectable:hover {
  background-color: #69c0ff;
}

.selected {
  background-color: #91d5ff;
}

.unselectable {
  opacity: 0.5;
  background-color: #bfbfbf;
}

.rerolled {
  background-color: #ffbb96;
}

.from-reroll {
  border: 1px solid #ffbb96 !important;
}

.theme-reroll.selectable:hover {
  background-color: #ff7a45;
}

.theme-reroll.selected {
  background-color: #ffbb96;
}
